$mdc-theme-primary: #ff5d84;
$mdc-theme-secondary: #feeae6;
$mdc-theme-on-primary: #442b2d;
$mdc-theme-on-secondary: #442b2d;

@import "@material/react-layout-grid/index.scss";
@import "@material/react-top-app-bar/index.scss";
@import "@material/react-material-icon/index.scss";
@import "@material/react-card/index.scss";
@import "@material/react-select/index.scss";
@import "@material/react-button/index.scss";
@import "@material/react-fab/index.scss";
@import "@material/react-tab/index.scss";
@import "@material/react-tab-bar/index.scss";
@import "@material/react-tab-scroller/index.scss";

.app_bg {
  background: url('/extra_bg2.png') repeat-y center center fixed;
  background-size: contain;
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  height:100%;

  .buttons_section {

    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(rgba(255,0,0,0), #292a7e, #292a7e);
    height: 250px;
    padding-top: 95px;

    button.sign_in {
      margin-top: 100px !important;
      font-size: 14px;
      font-weight: bold;
      width: 158px;
      height: 38px;
      display:block;
      margin: 0 auto;
      @include mdc-button-ink-color(#080653);
      @include mdc-button-container-fill-color(#ff5d84);
      @include mdc-button-shape-radius(50px);
    }

    button.sign_up {
      font-size: 14px;
      font-weight: bold;
      width: 158px;
      height: 38px;
      display:block;
      margin: 0 auto;
      @include mdc-button-ink-color(#ffbbcb);
      @include mdc-button-outline-color(#ffbbcb);
      @include mdc-button-shape-radius(50px);
    }

  }
}

.Page {
  background: #f1f3f4;
  height: 100vh;

  .mdc-card {
    @include mdc-card-shape-radius(10px);

    .mdc-card__primary-action {
      border-bottom: 1px solid #ccc;
    }

    h1 {
      font-family: 'Mukta Mahee', sans-serif;
      text-align: center;
      font-size: 60px;
      font-weight: bold;
      color: #292a7e;
      margin: 0;
    }

    h4 {
      font-family: 'Mukta Mahee', sans-serif;
      text-align: center;
      font-size: 12px;
      color: #666;
    }

    .content {
      font-family: 'Mukta Mahee', sans-serif;
      text-align: center;
      font-size: 17px;
      line-height: 28px;
      margin: 25px;
      color: #3c4043;
    }
  }
}

.mdc-top-app-bar {
  background: #f1f3f4;
  position: relative;

  .mdc-top-app-bar__title {
    padding-left: 0;
    margin: 0 auto;
    font-family: 'Knewave', cursive;
    color: #ff5e84;
    font-size: 32px;
  }
}

.mdc-tab-bar {
  position: absolute;
  bottom: 0;
  background: #fff;

  .mdc-tab-scroller {
    overflow-y: visible;

    .mdc-tab-scroller__scroll-area {
      overflow: visible;
    }

    .app-fab {
      margin-top: -15px;

      .mdc-tab__ripple {
        width: 0;
      }
    }

  }

}
